@media (max-width: 320px) {
  .footer {
    padding-bottom: 33px;
  }
}

@media (min-width: 1024px) {
  .footer {
    padding-bottom: 66px;
  }
}

.footer:after {
  content: " ";
  display: table;
  clear: both;
}

.footer__agency {
  float: left;
  text-transform: uppercase;
  color: #191926;
  letter-spacing: 0;
}

.footer__soc {
  float: right;
}

.soc {
  margin-top: 5px;
  margin-top: calc(0px + (5 - 0) * ((100vw - 320px) / (1024 - 320)));
  font-size: 0;
}

@media (max-width: 320px) {
  .soc {
    margin-top: 0px;
  }
}

@media (min-width: 1024px) {
  .soc {
    margin-top: 5px;
  }
}

.soc__item {
  vertical-align: middle;
  display: inline-block;
}

.soc__item:not(:first-child) {
  margin-left: 30px;
  margin-left: calc(15px + (30 - 15) * ((100vw - 320px) / (1024 - 320)));
}

@media (max-width: 320px) {
  .soc__item:not(:first-child) {
    margin-left: 15px;
  }
}

@media (min-width: 1024px) {
  .soc__item:not(:first-child) {
    margin-left: 30px;
  }
}

.soc__link {
  display: block;
}
